button{

    padding: 1em;
    border-radius: 1em;
    overflow: hidden;
    cursor: pointer;
}
button:hover.darkMode{
    background-color: aquamarine;
    
}
button.lightMode,.lightMode button{
    background-color:cadetblue;
    border: 1px solid cadetblue;
    color:black;
   
}
button.darkMode,.darkMode button{
    background-color: rgb(20, 91, 33);
    border: rgb(20, 91, 33);
    color:white;
}
