.dark{
    background-color: gray;
    color:white;
}

.container{
    width:15em;
    margin: 0 0.2em;
    float: left;
    position: relative;
    margin-bottom:20px;
    
}
  .pic img{
    width: 100%;
  }
  .title{
    font-weight: bold;
    margin-bottom: 1em;
    
  }
  .channel, .views{
    font-size: small;
    
    margin-bottom: 0.2em;
  }
.container.lightMode{
  color:black;

}
.container.darkMode{
  color:white;
}
  .views span{
    padding: 0 0.5em;
  }
  .cancel{
    position:absolute;
    right:0;
  }
  .edit{
    position:absolute;
    right:3em;

  }
